import { CircularProgressbar, buildStyles } from "react-circular-progressbar";
import "react-circular-progressbar/dist/styles.css";
import PlayButton from "./PlayButton";
import PauseButton from "./PauseButton";
import ContinueButton from "./ContinueButton";
import { useState, useEffect, useRef } from "react";
import ResetButton from "./ResetButton";
import VolumeButton from "./VolumeButton";
import { clearInterval, setInterval } from 'worker-timers';

function Timer() {

    const [isPaused,setIsPaused] = useState(true);
    const [currentPhase,setPhase] = useState(0);
    const [phaseName,setPhaseName] = useState("Pre-Test");
    const [secondsLeft, setSecondsLeft] = useState(900);
    const [totalSecs, setTotalSecs] = useState(900);
    const [isDone, setIsDone] = useState(false);
    const [curColour, setCurColour] = useState("#71a5d9");

    let audio = new Audio(require("./alarm.mp3"))
    let beep = new Audio(require("./ping.mp3"))

    const play_alarm = () => {
        audio.autoplay= true;
        audio.play()
    }
    const play_beep = () => {
        beep.autoplay= true;
        beep.play()
    }
  

    const secondsLeftRef = useRef(secondsLeft);
    const isPausedRef = useRef(isPaused); 
    const currentPhaseRef = useRef (currentPhase);
    const phaseNameRef = useRef(phaseName);
    const totalSecsRef = useRef(totalSecs);
    const isDoneRef = useRef(isDone);
    const curColourRef = useRef(curColour);



    function tick(){
        secondsLeftRef.current = secondsLeftRef.current - 1;
        setSecondsLeft(secondsLeftRef.current);
        console.log(secondsLeftRef.current)

    }

    function resetState(curPhase){

        isDoneRef.current = false;
        setIsDone(isDoneRef.current);

        curColourRef.current = "#71a5d9";
        setCurColour(curColourRef.current);

        switch (curPhase) {
            case 0:
                phaseNameRef.current = "Pre-Test";
                setPhaseName(phaseNameRef.current);


                secondsLeftRef.current = 900;
                setSecondsLeft(secondsLeftRef.current);

                totalSecsRef.current = 900;
                setTotalSecs(totalSecsRef.current);
                
                break;
            case 1:
                phaseNameRef.current = "Learning Module";
                setPhaseName(phaseNameRef.current);
                

                secondsLeftRef.current = 600;
                setSecondsLeft(secondsLeftRef.current);

                totalSecsRef.current = 600;
                setTotalSecs(totalSecsRef.current);
                
                break;
            case 2:
                phaseNameRef.current = "Post-Test";
                setPhaseName(phaseNameRef.current);
                    
    
                secondsLeftRef.current = 900;
                setSecondsLeft(secondsLeftRef.current);

                totalSecsRef.current = 900;
                setTotalSecs(totalSecsRef.current);
                    
                break;
            case 3:
                phaseNameRef.current = "MRT Part One";
                setPhaseName(phaseNameRef.current);
                    
    
                secondsLeftRef.current = 300;
                setSecondsLeft(secondsLeftRef.current);
                
                totalSecsRef.current = 300;
                setTotalSecs(totalSecsRef.current);
                    
                break;
            case 4:
                phaseNameRef.current = "MRT Part Two";
                setPhaseName(phaseNameRef.current);
                    
    
                secondsLeftRef.current = 300;
                setSecondsLeft(secondsLeftRef.current);
                
                totalSecsRef.current = 300;
                setTotalSecs(totalSecsRef.current);
                break;
            case 5:
                phaseNameRef.current = "VR Demo";
                setPhaseName(phaseNameRef.current);
                        
        
                secondsLeftRef.current = 300;
                setSecondsLeft(secondsLeftRef.current);
                    
                totalSecsRef.current = 300;
                setTotalSecs(totalSecsRef.current);
                break;

        }
        return;

    }
    
    useEffect(() => {

        //initTimer();

        var start = new Date();
 
        let interval = setInterval(() =>{
            const check = Math.round(secondsLeftRef.current)

            if (isPausedRef.current || isDoneRef.current === true){
                return;
            }
            if (secondsLeftRef.current === 0){
                play_beep();
                secondsLeftRef.current = 0;
                setSecondsLeft(secondsLeftRef.current);

                isPausedRef.current = true;
                setIsPaused(isPausedRef.current);

                isDoneRef.current = true;
                return;
            }
            else if(currentPhaseRef.current===0 && check === 300){
                play_beep();
                curColourRef.current = "#eda540";
                setCurColour(curColourRef.current);
            } 
            else if(currentPhaseRef.current===1 && check === 300){
                play_beep();
                curColourRef.current = "#eda540";
                setCurColour(curColourRef.current);
            } 
            else if(currentPhaseRef.current===2 && check === 300){
                play_beep();
                curColourRef.current = "#eda540";
                setCurColour(curColourRef.current);
            } 
            
            //tick(); 
            var delta = (new Date() - start);
            console.log (delta)

            secondsLeftRef.current = secondsLeftRef.current - 1;
            setSecondsLeft(secondsLeftRef.current);


        },1000);

        return () => clearInterval(interval);

    });

    const totalSeconds = totalSecs
    const percentage = secondsLeft/totalSeconds*100
    const minutes = Math.floor(secondsLeft/60);
    let seconds = Math.floor(secondsLeft % 60);
    if (seconds < 10) seconds = '0'+seconds;

    return (
        <div>
            <span> {phaseNameRef.current}</span>
            <CircularProgressbar
                value={percentage}
                text={minutes + ":" + seconds}
                styles={buildStyles({
                rotation: 0,
                strokeLinecap: "butt",
                pathColor: curColourRef.current,
                textColor: "#fff",
                trailColor: "#d6d6d6",
                })}
            />
            <div style={{ marginTop: "20px" }}>
                {isPaused ?  <div><PlayButton onClick={() => {setIsPaused(false); isPausedRef.current=false;}} />
                <ResetButton  onClick={ () => {resetState(currentPhaseRef.current); }}/>
                <VolumeButton  onClick={ () => {play_beep(); }}/>
                </div> : (<div><PauseButton onClick={() => {setIsPaused(true); isPausedRef.current=true;audio.pause();}}/><ResetButton onClick={ () => {resetState(currentPhaseRef.current);}}/><VolumeButton  onClick={ () => {play_beep(); }}/></div>)}
            </div>
            
            <div style={{ marginTop: "20px" }}>
                <ContinueButton className="with-text" onClick={ () => {currentPhaseRef.current=(currentPhaseRef.current+1)%6; setPhase(currentPhaseRef.current);console.log(currentPhaseRef.current);audio.pause();resetState(currentPhaseRef.current);}} />
            </div>
        </div>
    );
}

export default Timer;
